body {
  font-family: "Gill Sans MT", "Roboto Light", sans-serif; }

a {
  color: inherit; }

.icon {
  display: inline-block;
  width: 1em;
  height: 1em;
  position: relative; }

h2 {
  color: rgba(0, 0, 0, 0.1);
  font-family: "Gill Sans MT", "Roboto Light", sans-serif;
  font-size: 40px;
  text-transform: uppercase;
  text-align: center;
  position: relative;
  font-weight: 400;
  letter-spacing: 7px; }
  h2:before {
    content: 'divine';
    color: #000;
    font-size: .433em;
    letter-spacing: 20px;
    text-transform: uppercase;
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%); }
  h2:after {
    content: '';
    background-color: #000;
    display: block;
    width: 90%;
    height: 1px;
    position: absolute;
    top: 50%;
    left: 0;
    z-index: 10; }
  h2:nth-child(2n):after {
    left: 10%; }
  @media (min-width: 480px) {
    h2 {
      font-size: 50px; } }
  @media (min-width: 640px) {
    h2 {
      font-size: 72px; } }
  @media (min-width: 768px) {
    h2 {
      font-size: 80px; } }
  @media (min-width: 980px) {
    h2 {
      font-size: 90px; } }
  @media (min-width: 1400px) {
    h2 {
      font-size: 100px; } }
